body {
  background-color: #f6f6f6;
  font-family: 'Encode Sans Semi Expanded', sans-serif;
}
nav {
  display: flex;
  justify-content: center;
  background-color: #cfd8dc;
  padding: 15px;
}
.link{
  padding: 10px 40px;
  text-decoration: none;
  color: black;
  text-transform: uppercase;
}
.products {
  display: flex;
  flex-wrap: wrap;
}

.product-card {
  padding: 2%;
  flex-grow: 1;
  flex-basis: 16%;
}
.product-info{
  font-family: 'Encode Sans Semi Expanded', sans-serif;
}
.productImg{
  width: 300px;
  height: 400px;
  object-fit: cover;
  object-position: center;
}
.cont {
  display: flex;
  justify-content: center;
}

.back {
  background-color: #f4eddd; 
  padding: 20px;
  margin-left: 20px;
  margin-right: 20px;
  font-family: 'Encode Sans Semi Expanded', sans-serif;
}

.change {
  background-color: black;
  color: white;
  padding: 10px;
  border: none;
  font-family: 'Encode Sans Semi Expanded', sans-serif;
}

h3, h4 {
  font-family: 'Encode Sans Semi Expanded', sans-serif;
}

button {
  margin: 10px;
}